<template>
  <div>
    <div class="flex justify-between items-center mb-8">
      <Breadcrumb
        pageTitle="Payouts"
        :backTo="{ name: 'setting' }"
        :items="breadcrumb"
      />
    </div>

    <Card class="relative">
      <div class="flex items-center justify-between gap-x-4 mb-6">
        <div class="flex flex-1 items-center gap-x-2 border-b border-grey-01">
          <div
            v-for="tab in tabs"
            :key="tab.id"
            :class=" tabActive.includes(tab.to.name) ? 'text-teal-04 border-teal-04 border-b-4 font-bold' : 'text-grey-02'"
            class="custom-tabs px-2 py-3 hover:border-teal-04 hover:text-teal-04"
            @click="handleChangeTab(tab.to)">
            {{ tab.name }}
          </div>
        </div>
        <div v-if="tabActive.includes('PromoCode')">
          <Button
            color="teal"
            class="lg:mt-0 mt-4 ml-auto"
            :to="{ name: 'settingPayoutsPromoCodeCreate' }"
            link>
            <span class="material-icons">add</span> Add Promo Code
          </Button>
        </div>
      </div>
      <router-view />
    </Card>

    <Card v-if="routeName === 'settingPayoutsMethod'" whitelabel="remove" class="mt-4">
      Can't find a payout method? Let us know and we will add it to our payout method.
      <Button class="ml-2" color="teal" @click="handleRequestPayoutMethod" dense>Request Payout Method</Button>
    </Card>
  </div>
</template>

<script>
import { defineComponent, ref, computed, onMounted, watch } from 'vue'

import Tab from 'bbUi/components/tab/tab.vue'
import Card from 'bbUi/components/card/card.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'
import Loading from 'bbUi/components/loading/loading.vue'
import Pagination from 'components/Pagination.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import Button from 'bbUi/components/button/button.vue'
import CustomPayout from 'components/CustomPayout/index.vue'

import { mapTabRoute } from 'utils/mapping'
import SelectLocation from 'components/PaymentMethod/SelectLocation.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import { useRoute, useRouter } from 'vue-router'

export default defineComponent({
  components: {
    Tab,
    Card,
    Checkbox,
    Input,
    Loading,
    Pagination,
    Breadcrumb,
    Button,
    CustomPayout,
    SelectLocation,
    Modal
  },
  async setup() {
    const route = useRoute()
    const router = useRouter()

    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Payouts'
      }
    ]
    const tabs = computed(() => {
      return [
        mapTabRoute({
          id: 0,
          short_name: 'Payout Method',
        }, 'settingPayoutsMethod', {
          tab: 'payment_method',
        }, 'id', 'short_name'),
        mapTabRoute({
          id: 2,
          short_name: 'Promo Code',
        }, 'settingPayoutsPromoCode', {
          tab: 'promo_code'
        }, 'id', 'short_name')
      ]
    })
    const routeName = computed(() => route.name)
    const tabActive = ref(undefined)
    const handleChangeTab = (to) => { router.push(to) }

    const handleRequestPayoutMethod = () => window.open(
      'https://forms.clickup.com/8427959/f/816dq-50296/48L7CP8XE5Q2Q0A2MY',
      '_blank',
      'noreferrer noopener'
    )

    watch(
      () => route.name,
      () => tabActive.value = route.name,
      { immediate: true }
    )

    return {
      breadcrumb,
      tabs,
      routeName,
      tabActive,
      handleChangeTab,
      handleRequestPayoutMethod
    }
  }
})
</script>

<style lang="scss" scoped>
.custom-tabs:hover {
  font-weight: bold;
  border-bottom-width: 4px;
  transition: all .3s ease-in-out;
  cursor: pointer;
}
</style>
